@media (min-width: 600px)  {
body{
    background-image: url("./bg1.jpg");
    background-size:1300px 900px ;
    background-position: center;
    background-attachment: fixed;
}
.bgg{
background-color: #ccb3b3a8;
min-height:400px;
width: 1225px;
}
*{
margin-right:60px;
}
header{
background-color: #d30000;
width: 100%;
text-align:justify;
height: 40px;
padding-top: 12px;
color: white;
position: fixed;
word-spacing: normal;
}
input[type="button"]{
    background-color: #d30000;
    border-radius: 4px;
    border-color: #d30000;
    margin-top: 6px;
    color: white;
    height: 30px;
    margin-left: 0px;
}
li{
    list-style: none;
}
.nv{
    background-color: rgba(75, 75, 75, 0);
    border-color:rgba(75, 75, 75, 0) ;
    color: rgb(200, 200, 200);
}
.nv1{
    background-color: rgba(75, 75, 75, 0);
    border-color:rgba(75, 75, 75, 0) ;
    
    color: white;
text-decoration-line: line-through;
}
.done{
    background-color:#3b3b3b;
    border-radius: 100px;
    border-color:  #3b3b3b;
    margin-top: 6px;
    color: white;
    width: 30px;
    height: 30px;
    float: right;
    margin-right: 4px;
    padding: 0px;
}
.normal{
background-color:  #ffffff00;
width: 100%;
height: 40px;
border-radius: 2px;
margin-left: 0px;


}
.new{
background-color: #d30000;
width: 100%;
height: 40px;
border-radius: 2px;
margin-left: 0px;
float: left;
color: white;
text-decoration-line: line-through;

}
.taches{
    background-color: white;
    width: 700px;
    display: inline-block;
    margin-left: 150px;
}
.hedd{
    background-color: #3b3b3b;
    height: 30px;
    width: 700px;
    font-style: oblique;
    color: white;
    margin-left: 150px;
}

.am{
    margin-left: 200px;
    border-color: #ffffff9c ;
    border-radius: 6px;
    width: 220px;
    height: 30px;
    color: #aa9595;
    background-color:#ffffff9c ;
    margin-right: 0px;
}
.suppe{
    background-color: #d30000;
    border-radius: 10px;
    border-color: #d30000;
    color: rgb(54, 19, 19);
    width: 30px;
    float: right;
    height: 30px;
    padding: 0px;
    text-align: center;
    margin-left: 0px;
}
.ico{
    margin: 0px;
    size: 22px;
}

.rr{
    height: 100px;
}}
